.btn {
    background: linear-gradient(to top right, #ffc107 -5%, #FF5733 100%);
    transition: 0.5s;
}

.btn:hover {
    background: linear-gradient(to top right, #FF5733 -5%, #ffc107 100%);
    color: white;
    transition: 0.5s;
}

.socil_item_inner li {
    list-style: none;
}

.socil_item_inner li a {
    color: #fff;
    margin: 0px 15px;
    font-size: 14px;
}

.socil_item_inner {
    padding-bottom: 10px;
}

.map_sec {
    padding: 50px 0px;
}

.map_inner h4,
.map_inner p {
    color: #000;
    text-align: center;
}

.map_inner p {
    font-size: 13px;
}

.map_bind {
    margin-top: 50px;
    border-radius: 30px;
    overflow: hidden;
}

.map_bind iframe {
    width: 100%;
    height: 450px;
    border: 0;
}